const db = wx.cloud.database()
const _ = db.command
export default {
	data() {
		return {
			list: []
		};
	},
	methods: {
		investigation(id) {
			uni.showToast({
				icon: 'none',
				title: '问卷已成功派发给他',
				duration: 1000
			})
		},
		
		
		async getList() {
			try{
				let res = await db.collection('accounts').where({
					permissions: _.in([2])
				}).get()
				console.log(res)
				this.list = res.data
			}catch(e){
				console.log(e)
			}
		}
	},
	onLoad() {
		this.getList()
	}
}
